"use strict";
var ListsView = (function () {
    function ListsView() {
        this.view = document.getElementById('view');
    }
    ListsView.prototype.update = function (lists) {
        var list;
        var listsTemplate;
        listsTemplate = Object.keys(lists).map(function (name) {
            list = lists[name];
            return "<li class=\"list\" id=\"" + name + "\">\n\t\t\t\t" + list.name + "\n\t\t\t</li>";
        }).join('');
        var template = "\n\t\t<input id=\"listInput\" value=\"\" placeholder=\"neueListe\" type=\"text\">\n        <button id=\"addListButton\">Test</button>\n        <div id=\"lists\">" + listsTemplate + "<div>\n\t\t";
        this.view.innerHTML = template;
    };
    return ListsView;
}());
module.exports = ListsView;



